*{
    color: white;
    background-color: black;
  }
  

.githead{
    font-family:'Roboto', sans-serif;
    text-align: center;
    font-size: 36px;
    color:white;
    margin-top: 50px;
    width: 100%;
    margin: auto;
}

.githead-div{
    width: 90%;
    margin: auto;
  
}
.calendar{
    margin: auto;
    width: 100%;
    height: auto;
    margin-top: 60px;
    padding: 20px;
}

.calendar > img {
    width: 90%;
    margin: auto;
}
#lang{
    background: 1px solid red;
    margin: auto;
    width: 100%;
}

#github-top-langs{
    height: 100%;
    background: 1px solid red;
}
.stats{
    width:95%;
    margin: auto;
    display: flex;
    margin-top: 20px;
    height: 280px;
}

.stats > div {
    margin: auto;
    width: 45%;
    height: 200px;
}

.stats2{
    width: 90%;
    margin: auto;
    height: 90%;    
};

.stats3{
    height: 90%;
    width: 90%;
    margin: auto;
};


