.img1{
    border-radius: 8px;
    width: 100%;
    height: 100%;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.img2{
    border-radius: 8px;
    width: 100%;
    height: 100%;
     box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; 
}
.img3{
    border-radius: 8px;
    width: 100%;
