     box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; 
};



@media only screen and (min-width:340px) and (max-width:480px){
    .stats1{

        width: 140%;
    }
    .stats2{
        width: 140%;
    }
    .stats3{
       
        width: 140%;
    }
};

  @media only screen and (min-width:481px) and (max-width:890px){
    .stats1{
        width: 100%;
    }
    .stats2{
        width: 100%;
    }
    .stats3{
       
        width: 100%;
    }
  };


  @media only screen and (min-width:890px) and (max-width:1580px){
    .calendar{
        width:100%;
        margin: auto;
       
        margin-top: 60px;
        
    }
  };